.seccion {
    margin-top: calc($separacion / 2);
    margin-bottom: calc($separacion / 2);
}

.contenido-centrado {
    max-width: 800px;

}

.imagen-internas {
    display: block;
    max-width: 70%;
    height: auto;
    margin: 0 auto;
}

.flex {
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    align-items: center;

    @include tablet {
        flex-direction: row;
    }
}

.w-100 {
    width: 100%;
}