.main-admin {
    overflow: overlay;
    text-align: center;

}

.propiedades {
    margin-top: 4rem;
    width: 100%;
    border-spacing: 0;


    thead {
        background-color: $verde;

        th {
            color: $blanco;
            padding: 2rem;
        }
    }

    tbody {
        text-align: center;
    }
}


.alerta {
    padding: .5rem;
    text-align: center;
    font-weight: $bold;
    text-transform: uppercase;
    color: $blanco;
    margin: 2rem 0;

    &.error {
        background-color: $rojo;
    }

    &.exito {
        background-color: $verde;
    }

    &.actualizar {
        background-color: $amarillo;
    }
}

.imagen-small {
    display: block;
    width: 15rem;
}